*,
:after,
:before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

body {
  background-color: #22262a;
}

.App {
  background-color: #22262a;
  display: flex;
  justify-content: center;
}

.container-profile {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.container-button {
  position: absolute;
  top: 83.5%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.container-contact {
  position: absolute;
  top: 40%;
  left: 1.5%;
  transform: translate(-50%, -50%);
}

.container-navigation {
  position: fixed;
  top: 2.3%;
  right: 2.3%;
  z-index: 1000;
  width: 4.2em;
  cursor: pointer;
}
.container-portfolio {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

@media screen and (min-width: 1024px) {
  .container-contact {
    top: 45%;
    left: 1.5%;
  }
  .container-navigation {
    top: 3.3%;
    right: 3.3%;
  }
  .container-button {
    top: 83.5%;
    left: 50%;
  }
  .container-profile {
    top: 45%;
    left: 50%;
  }

  .container-portfolio {
    top: 45%;
    left: 50%;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .container-profile {
    top: 50%;
    left: 60%;
  }
  .container-button {
    top: 80%;
    left: 60%;
  }
  .container-contact {
    top: 45%;
    left: 2%;
  }
  .container-portfolio {
    top: 50%;
    left: 60%;
  }
}

@media only screen and (min-width: 321px) and (max-device-width: 768px) {
  .container-profile {
    top: 45%;
    left: 55%;
  }
  .container-button {
    top: 75%;
    left: 55%;
  }

  .container-contact {
    top: 45%;
    left: -14%;
  }

  .container-contact-portafolio {
    top: 45%;
    left: -14%;
  }
  .container-navigation {
    top: 3%;
    right: 6%;
  }
  .container-portfolio {
    top: 40%;
    left: 58%;
  }
}

@media only screen and (max-width: 320px) {
}

::-webkit-scrollbar-track {
  border-radius: 12px;
  background-color: #171a1d;
}

::-webkit-scrollbar-thumb {
  border-radius: 12px;
  background-color: #97bfd3;
}

::-webkit-scrollbar {
  width: 6px;
  background-color: #171a1d;
  border-radius: 3px;
}
